.carousel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 2em;
}

.carousel-container {
  display: flex;
  align-items: center;

}

.card {
  width: 70%;
 height: 60%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 4.2em;
}

.card img {
  margin-top:3em ;
  max-width: 30em;
  max-height: 90%;
  object-fit: cover;
}

@media only screen and (max-width:1300px) {
 .carousel{margin-right: -15em;
  }
}
@media only screen and (max-width:600px) {
  .carousel{margin-top:-4em;
    margin-left: -2em;
    margin-bottom: -3em;
  transform: scale(0.7);
  }}
